    position: fixed;
    left: 0;
    top:0;
    width: 100%;
    background-color:  rgb(21, 98, 161);
    height: 100%;
    z-index: 50;
    overflow-y: auto;
    grid-row-gap: 10px;
    row-gap: 10px;
    
  }
  .nav-list-container{
    background-color: var(--background);
    
  }

  .nav-list li::after {
    display: none;
    
  }
  
  .nav-list li {
    list-style: none;
    text-align: center;
    height: 60px;
    
  }

  
  .nav-list li a {
    text-decoration: none;
    color: #fff;
    font-size: 1.6rem;
    position: relative;
    
  }
  
  .nav-list li a:hover{
    color: #ccc;
    
  }
  
  .btn-logout{
    border: none;
    cursor: pointer;
    background-color: #fff;
    padding:.7em 1em;
    font-weight: 700;
    font-size: 1em;
    
  }
  
  .btn-logout:hover{
    background-color: rgb(223, 250, 252);
    
  }
  .profile-picture{
    width:50px;
    border-radius: 50%;
    display: inline-block;
    margin-left: 10px;
    
  }
  
  .userInfo{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5em;
    margin-left: 20px;
    
  }

  .show{
    display: flex;
    -webkit-animation-name: showMenu;
            animation-name: showMenu;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    

  }

  @-webkit-keyframes showMenu{
    from{
      left: 100%;
    }
  
    to{
      left: 0;
    }
  }

  @keyframes showMenu{
    from{
      left: 100%;
    }
  
    to{
      left: 0;
    }
  }

}
